<template>
  <div>
    <navMedic/>
    <div class="container bg-light">
      <h1 class="text-center text-dark p-5">
        <b>Datos del Médico</b>
      </h1>
      <h3 class="text-center text-dark">Realice los cambios que desea modificar</h3>
      <div style="width:400px">
        <b-alert show variant="danger" style="width:400px" v-if="isCorrect!==null">
          <h4 class="alert-heading">{{avise}}</h4>
          <p>{{message}}</p>
        </b-alert>
      </div>
      <form @submit="save">
        <div class="form-row p-5">
          <div class="col-md-4">
            <label for="tipoDocumento">Tipo de Documento</label>
            <select
              class="custom-select"
              id="tipoDocumento"
              v-model.number="tipoDocumento.id"
              required
            >
              <option disabled value>--Seleccione--</option>
              <option value="1">Pasaporte</option>
              <option value="2">Cedula</option>
            </select>
          </div>
          <div class="col-md-4">
            <label for="numDocumento">Numero de Documento</label>
            <input
              class="form-control"
              placeholder="Numero de documento"
              maxlength="10"
              v-model="medic.numDocumento"
              type="text"
              required
            >
          </div>
          <div class="col-md-4">
            <label for="especialidad">Especialidad</label>
            <select
              class="custom-select"
              id="especialidad"
              v-model.number="especialidad.id"
              required
            >
              <option disabled value>--Seleccione--</option>
              <option value="1">Alergiología</option>
              <option value="2">Cardiología</option>
              <option value="3">Dermatología</option>
              <option value="4">Fisiatría</option>
              <option value="5">Flebología</option>
              <option value="6">Geriatría</option>
              <option value="7">Ginecología</option>
              <option value="8">Mastología - Oncología</option>
              <option value="9">Medicina General</option>
              <option value="10">Medicina Clínica</option>
              <option value="11">Pediatría</option>
              <option value="12">Psicología</option>
              <option value="13">Psiquiatría</option>
              <option value="14">Neumología</option>
              <option value="15">Neurología</option>
              <option value="16">Nutrición</option>
              <option value="17">Odontología</option>
              <option value="18">Oftamología</option>
              <option value="19">Optometría</option>
              <option value="20">Otorrinolaringología</option>
              <option value="21">Reumatología</option>
              <option value="22">Traumatología</option>
              <option value="23">Urología</option>
              <option value="24">Terapia de Lenguaje</option>
              <option value="25">Terapia Respiratoria</option>
              <option value="26">Terapia con Oxígeno</option>
            </select>
          </div>
        </div>
        <div class="form-row p-5">
          <div class="col-md-6">
            <label for="nombre">Nombres</label>
            <input
              class="form-control"
              placeholder="Ingrese nombres"
              v-model="medic.nombre"
              type="text"
              required
            >
          </div>
          <div class="col-md-6">
            <label for="nombre">Apellidos</label>
            <input
              class="form-control"
              placeholder="Ingrese apellidos"
              v-model="medic.apellido"
              type="text"
              required
            >
          </div>
        </div>
        <div class="form-row p-5">
          <div class="col-md-6">
            <label for="numTelefono">Numero Telefono</label>
            <input
              class="form-control"
              placeholder="Numero de telefono"
              v-model="medic.numTelefono"
              type="text"
            >
          </div>
          <div class="col-md-6">
            <label for="numTelefono">Numero Celular</label>
            <input
              class="form-control"
              placeholder="Numero de celular"
              v-model="medic.numCelular"
              type="text"
              required
            >
          </div>
        </div>
        <div class="form-row p-5">
          <button class="btn btn-primary">Enviar</button>
        </div>
      </form>
    </div>
    <Footer/>
  </div>
</template>
<script>
import { URL, bootbox } from "@/variables.js";
import 'bootstrap/dist/css/bootstrap.css'
import 'bootstrap-vue/dist/bootstrap-vue.css'
import axios from "axios";
import navMedic from "@/components/medic/navMedic.vue";
import Footer from "@/components/Footer.vue";
export default {
  name: "editarMedico",
  data() {
    return {
      id: this.$route.params.id,
      medic: [],
      message: null,
      isCorrect: null,
      avise: null,
      tipoDocumento: {
        id: null
      },
      especialidad:{
        id: null
      }
    };
  },
  methods: {
    async getMedicById() {
      axios
        .get(`${URL}/api/medico/${this.id}`)
        .then(response => {
          this.tipoDocumento = response.data.tipoDocumento;
          this.especialidad=response.data.especialidad;
          this.medic = response.data;
        })
        .catch(error => {
          bootbox.alert({
            title: "Error",
            message: error.response.data.mensaje,
            locale: "es",
            backdrop: true,
            callback: this.$router.push("/medicos")
          });
          console.log(error);
        });
    },
    save(e) {
      e.preventDefault();
      this.avise = "";
      this.message = "";
      axios
        .put(`${URL}/api/medico/${this.id}`, {
          tipoDocumento: { id: this.tipoDocumento.id },
          especialidad:{id:this.especialidad.id},
          apellido: this.medic.apellido,
          nombre: this.medic.nombre,
          numDocumento: this.medic.numDocumento,
          numTelefono: this.medic.numTelefono,
          numCelular: this.medic.numCelular
        })
        .then(response => {
          bootbox.alert({
            title: "Datos Actualizados",
            message: response.data.mensaje,
            locale: "es",
            backdrop: true,
            callback: this.$router.push("/medicos")
          });
        })
        .catch(error => {
          this.avise = "Error";
          this.isCorrect = false;
          this.message = "Detalle: " + error.response.data.mensaje;
          bootbox.alert({
            title: "Error",
            message: "No se pudo editar",
            locale: "es",
            backdrop: true
          });
          console.log(error);
        });
    }
  },
  components: {
    navMedic,
    Footer
  },
  mounted() {
    this.getMedicById();
  }
};
</script>

